.mainGameWrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: stretch;
  overflow: hidden;
}
.mainGameBoard {
  width: 70vw;
  height: 100vh;
  flex: 1 1;
  display: flex;
  flex-direction: column;
  background-color: var(--black_5);
  overflow: hidden;
  overflow-y: auto;
  box-sizing: border-box;
  justify-content: space-between;
}
.mainGameQuestionsBlock {
  width: 100%;
  padding: 10% 2% 6% 10%;
}
.mainGameQuestion {
  margin: 0;
}
.mainGameAnswers {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  padding-left: 10%;
  padding-bottom: 100px;
}
.mainGameScoreContainer {
  display: flex;
  padding: 10px 0;
  width: 30vw;
  min-width: 376px;
  overflow: hidden;
  overflow-y: auto;
  background: #fff;
}
.mainGameScoreWrap {
  width: 100%;
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
}
.menu {
  display: none;
}

@media (max-width: 767px) {
  .mainGameWrapper {
    overflow-x: auto;
  }
  .mainGameBoard {
    width: 100%;
    height: 100vh;
    padding: 20px 0 32px;
    align-items: center;
  }
  .mainGameQuestionsBlock {
    text-align: center;
    padding: 0;
    padding-left: 40px;
    padding-right: 40px;
    margin-top: 0;
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .mainGameQuestions {
  }
  .mainGameAnswers {
    justify-content: center;
    padding: 0;
  }
  .mainGameScoreContainer {
    width: 100%;
    min-width: 100%;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transition: 0.3s;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    z-index: 99;
  }
  .mainGameScoreContainer.open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  .menu svg:last-child {
    opacity: 0;
  }
  .menu.open svg:last-child {
    opacity: 1;
  }
  .menu.open svg:first-child {
    opacity: 0;
  }
}

.answerDisabled {
  position: relative;
}
.answerDisabled.correct:before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: -17px;
  right: -17px;
  z-index: 10;
  background: transparent;
}
.answerWrap {
  position: relative;
  width: 371.77px;
  cursor: pointer;
  margin: 0 17px 32px 17px;
  z-index: 2;
}
.answerWrap.selected path {
  stroke: var(--orange_80) !important;
  fill: var(--orange_5) !important;
}
.answerWrap.selected:hover:before {
  background-color: var(--orange_80) !important;
}
.answerWrap.correct path {
  stroke: var(--green_100) !important;
  fill: var(--green_5) !important;
}
.answerWrap.correct:hover:before {
  background-color: var(--green_100);
}
.answerWrap.wrong path {
  stroke: var(--red_100);
  fill: var(--red_5);
}
.answerWrap.wrong:hover:before {
  background-color: var(--red_100);
}

.answerWrap:before {
  content: '';
  display: block;
  height: 1px;
  width: 405px;
  position: absolute;
  top: 47%;
  left: -17px;
  background-color: #ccc;
  transition: 0.3s;
  z-index: -1;
}
.answerContent {
  margin: 0;
  position: absolute;
  top: 25px;
  left: 32px;
  width: 330px;
  text-overflow: ellipsis;
  overflow: hidden;
  /* white-space: nowrap; */
}
.answerLetter {
  font-family: 'Inter', sans-serif;
  font-size: 20px;
  font-weight: 600;
  color: var(--orange_100);
  margin-right: 8px;
}
.answerLine {
  display: none;
  position: absolute;
  top: 50%;
  width: 100%;
  height: 1px;
  background-color: var(--black_40);
  z-index: 1;
  transition: 0.3s;
}

.svgWrap {
  z-index: 1;
}
.svgWrap svg:first-child {
  display: block;
}
.svgWrap svg:last-child {
  display: none;
}
.answerWrap:hover path {
  stroke: var(--orange_100);
}

.answerWrap:hover:before {
  background-color: var(--orange_100);
}
.menu {
  display: none;
}
@media (max-width: 767px) {
  .answerDisabled {
    width: 100%;
  }
  .answerWrap {
    margin: 8px 0 0;
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .answerWrap:before {
    display: none;
  }
  .answerContent {
    width: 240px;
    top: 31%;
    left: calc(50% - 120px);
    z-index: 3;
  }
  .answerLine {
    display: block;
    background-color: var(--black_40);
  }

  .svgWrap svg:first-child {
    display: none;
  }
  .svgWrap svg:last-child {
    display: block;
  }
  .answerWrap:before {
    display: none;
  }
  .answerLine.selected {
    background-color: var(--orange_80);
  }
  .answerLine.correct {
    background-color: var(--green_100);
  }
  .answerLine.wrong {
    background-color: var(--red_100);
  }
  .menu {
    display: block;
    z-index: 100;
  }
  .menu svg {
    position: absolute;
    top: 21px;
    right: 21px;
    transition: 0.3s;
  }
  .menu svg:last-child {
    opacity: 0;
  }
}

.roundsBlock {
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  margin-bottom: 7px;
}
.roundsBlock.current path {
  stroke: var(--orange_100);
}
.roundsBlock.riched path {
  stroke: var(--black_40);
}

.roundsBlock.current .roundsContent {
  color: var(--orange_100);
}
.roundsBlock.current .roundsLine {
  background-color: var(--orange_100);
}
.roundsBlock.riched .roundsContent {
  color: var(--black_40);
}
.roundsBlock.riched .roundsLine {
  background-color: var(--black_40);
}
.roundsContent {
  position: absolute;
  margin: 0;
  padding: 8px;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -55%);
          transform: translate(-50%, -55%);
  z-index: 3;
  font-family: 'Inter', sans-serif;
}

.roundsLine {
  position: absolute;
  top: 46%;
  width: 100%;
  height: 1px;
  background-color: var(--black_40);
  z-index: 1;
}
.roundsSvgWarp {
  z-index: 2;
}
.roundsSvgWarp svg:first-child {
  display: block;
}
.roundsSvgWarp svg:last-child {
  display: none;
}
@media (max-width: 767px) {
  .roundsSvgWarp svg:first-child {
    display: none;
  }
  .roundsSvgWarp svg:last-child {
    display: block;
  }
  .roundsContent {
    padding: 0;
  }
}

.finishGameWrapper {
  background: var(--black_5);
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  padding: 80px;
  width: 100vw;
  height: 100vh;
}
.finishGameImg {
  height: auto;
  width: 100%;
}
.finishGameLogic {
  align-self: center;
  align-items: flex-start;
  display: flex;
  font-weight: 600;
  flex-direction: column;
  text-align: left;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.finishGameTotal {
  color: #88888c;
  font-size: 32px;
  margin: 0;
}
.finishGameScore {
  font-size: 56px;
  margin-bottom: 65px;
}
.finishGameButton {
  background: var(--orange_100);
  border-radius: 12px;
  color: #fff;
  font-size: 20px;
  text-decoration: none;
  padding: 15px 100px;
  cursor: pointer;
}
.finishGameButton:hover {
  background-color: var(--orange_80);
}
.finishGameButton:active {
  background: var(--orange_120);
}

@media (max-width: 767px) {
  .finishGameWrapper {
    grid-template-columns: 1fr;
    grid-template-rows: 50% 50%;
    padding: 0;
  }
  .finishGameImg {
    align-self: end;
    justify-self: center;
    width: 90%;
  }
  .finishGameLogic {
    align-self: start;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    width: 100%;
    height: 100%;
  }
  .finishGameTotal {
    margin-top: 40px;
    font-size: 20px;
  }
  .finishGameScore {
    font-size: 32px;
    margin-bottom: 100px;
  }
  .finishGameButton {
    padding: 10px 50px;
    margin-bottom: 50px;
    width: 90%;
  }
}

.startGameWrapper {
  background: linear-gradient(to bottom right, #fff 50%, var(--orange_5) 0);
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  padding: 80px;
  width: 100vw;
  height: 100vh;
}
.startGameImg {
  height: auto;
  width: 100%;
}
.startGameLogic {
  align-self: center;
  align-items: flex-start;
  display: flex;
  font-weight: 600;
  flex-direction: column;
  text-align: left;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.startGameTitle {
  margin-bottom: 100px;
}
.startGameButton {
  background: var(--orange_100);
  border-radius: 12px;
  color: #fff;
  font-size: 20px;
  text-decoration: none;
  padding: 15px 100px;
  cursor: pointer;
}
.startGameButton:hover {
  background-color: var(--orange_80);
}
.startGameButton:active {
  background: var(--orange_120);
}
@media screen and (max-width: 767px) {
  .startGameWrapper {
    grid-template-columns: 1fr;
    grid-template-rows: 50% 50%;
    padding: 0;
  }
  .startGameImg {
    align-self: end;
    justify-self: center;
    width: 90%;
    margin-bottom: 25px;
  }
  .startGameLogic {
    align-self: start;
    align-items: center;
    text-align: center;
    width: 100%;
    height: 100%;
  }
  .startGameTitle {
    margin-bottom: 100px;
  }
  .startGameButton {
    padding: 10px 50px;
    margin-bottom: 50px;
    width: 90%;
  }
}

